
//defines all icon properties//
[class^="icon-"]{
  display:block;
  width:1em;
  height:1em;
  background:url('../../images/icons/sprite.svg');
  background-size:none;
  text-indent: 100%;
  white-space: nowrap;
  overflow: hidden;
  z-index:1000;
  fill:white;
  
  margin-left: auto;
  margin-right: auto;
  //just an inspiration if want to fill icon with gradient/images. use mask
  //-webkit-mask: url('../../images/icons/sprite.svg');
 //mask: url('../../images/icons/sprite.svg');

}

